    .input-group .float-label.float-above {
      -webkit-transform-origin: top left;
              transform-origin: top left;
      -webkit-transform: translateY(-100%) scale(0.75);
              transform: translateY(-100%) scale(0.75);
      color: rgba(90, 0, 238, 0.87);
