.Bookmark {
  width: 150px;
  height: 150px;
  position: relative;
  background-color: black;
  border-radius: 4px;
  overflow: hidden;
  outline: none; }
  .Bookmark.empty {
    background-image: -webkit-linear-gradient(315deg, #323940 0%, #2B3137 100%);
    background-image: linear-gradient(135deg, #323940 0%, #2B3137 100%);
    border: 1px solid #202429;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer; }
    .Bookmark.empty .add-icon {
