      fill: #15181a;
      -webkit-animation: spin 340ms linear;
              animation: spin 340ms linear; }
    .Bookmark.empty:hover {
      background-color: #2B3137;
      background-image: none;
      border: 1px solid #1b1f23; }
    .Bookmark.empty:focus, .Bookmark.empty:active {
